@import url("https://fonts.googleapis.com/css?family=Clicker+Script|Playball|Stalemate&display=swap&subset=latin-ext");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.first-page {
  height: 100vh;
  width: 100vw;
  padding-top: 10vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(/img/first_page/tomato-2780424_1920_2_Easy-Resize.com.jpg);
  background-size: cover;
  font-family: "playball", sans-serif;
}

.first-page .app-title {
  margin: 10vw;
  font-size: 21vw;
  text-align: center;
  color: #fffdfe;
}

.first-page a {
  margin: 5vh 10vw;
  padding: 3vw;
  background-color: #f31c42;
  font-size: 8vw;
  text-decoration: none;
  text-align: center;
  color: #fff;
  border-radius: 25%;
}

.main-page {
  height: 100vh;
  width: 100vw;
}

.main-header {
  width: 100vw;
  height: 12.5vh;
  border: 2px solid black;
  background-size: cover;
  background-image: url(/img/first_page/tomato-2780424_1920_2_Easy-Resize.com.jpg);
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-header h2 {
  font-size: 16vw;
  color: #fffdfe;
  font-family: "playball", sans-serif;
}

div[class$="category"] {
  width: 100vw;
  height: 12.5vh;
  border: 2px solid black;
  background-size: cover;
}

div[class$="category"] a {
  width: 100vw;
  height: 12.5vh;
  display: flex;
  align-items: center;
  text-decoration: none;
  position: relative;
}

div[class$="category"] a h3 {
  font-size: 8vw;
  color: black;
  position: absolute;
}

.snack-category, .snack-recipes_title {
  background-image: url(/img/snack/tasting-cheese-dish-on-a-wooden-plate-picjumbo-com_Easy-Resize.com.jpg);
  background-position: 0% 30%;
}

.snack-category h3, .snack-recipes_title h3 {
  left: 55%;
}

.salad-category {
  background-image: url(/img/vege/louis-hansel-shotsoflouis-phEaeqe555M-unsplash_Easy-Resize.com.jpg);
  background-position: 50% 30%;
  transform: scale(-1, 1);
}

.salad-category h3 {
  transform: scale(-1, 1);
  right: 10%;
}

.meat-category {
  background-image: url(/img/meat_dish/louis-hansel-shotsoflouis-K47107aP8UU-unsplash_Easy-Resize.com.jpg);
  background-position: 40% 100%;
}

.meat-category h3 {
  left: 47%;
}

.vege-category {
  background-image: url(/img/vege/food-2068217_1920_Easy-Resize.com.jpg);
}

.vege-category h3 {
  left: 10%;
}

.soup-category {
  background-image: url(/img/soup/cayla1-w6ftFbPCs9I-unsplash_Easy-Resize.com.jpg);
  background-position: 0% 4%;
}

.soup-category h3 {
  left: 70%;
}

.sweet-category {
  background-image: url(/img/on_sweetly/three-yummy-donuts-with-room-for-text-picjumbo-com_Easy-Resize.com.jpg);
  background-position: 0% 45%;
}

.sweet-category h3 {
  left: 10%;
}

.smoothie-category {
  background-image: url(/img/smoothie/fresh-strawberries-bananas-smoothie-picjumbo-com_Easy-Resize.com.jpg);
  background-position: 0% 15%;
  transform: scale(-1, 1);
}

.smoothie-category h3 {
  transform: scale(-1, 1);
  right: 62%;
}

.snack-recipes_title {
  width: 100vw;
  height: 12.5vh;
  border: 2px solid black;
  background-size: cover;
  display: flex;
  align-items: center;
  position: relative;
}

.snack-recipes_title h3 {
  font-size: 8vw;
  color: black;
  position: absolute;
}
